	module.exports = {"data":{"markdownRemark":{"html":"<p>GraphQL is a query language for your API, and a server-side runtime for executing queries by using a type system you define for your data. GraphQL isn't tied to any specific database or storage engine and is instead backed by your existing code and data.</p>\n<p>A GraphQL service is created by defining types and fields on those types, then providing functions for each field on each type. For example, a GraphQL service that tells us who the logged in user is (me) as well as that user's name might look something like this:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-ruby\"><code><span class=\"token keyword\">require</span> <span class=\"token string\">'redcarpet'</span>\nmarkdown <span class=\"token operator\">=</span> <span class=\"token constant\">Redcarpet</span><span class=\"token punctuation\">.</span><span class=\"token keyword\">new</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"Hello World!\"</span><span class=\"token punctuation\">)</span>\nputs markdown<span class=\"token punctuation\">.</span>to_html\n</code></pre>\n      </div>","frontmatter":{"title":"Getting to know GraphQL"}}},"pathContext":{"slug":"/things-fall-apart/"}}

